@-webkit-keyframes rotating {
    from {
        /* -ms-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
        -o-transform: rotate(0deg); */
        transform: rotate(5520deg);
        -webkit-filter: blur(50px);
                filter: blur(50px);
        z-index: 0;
    }
    to {
        /* -ms-transform: rotateY(-8520deg) rotateX(-8520deg);
        -moz-transform: rotateY(-8520deg) rotateX(-8520deg);
        -webkit-transform: rotateY(-8520deg) rotateX(-8520deg);
        -o-transform: rotateY(-8520deg) rotateX(-8520deg); */
        transform: rotateX(-8820deg);
    }
}

@keyframes rotating {
    from {
        /* -ms-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
        -o-transform: rotate(0deg); */
        transform: rotate(5520deg);
        -webkit-filter: blur(50px);
                filter: blur(50px);
        z-index: 0;
    }
    to {
        /* -ms-transform: rotateY(-8520deg) rotateX(-8520deg);
        -moz-transform: rotateY(-8520deg) rotateX(-8520deg);
        -webkit-transform: rotateY(-8520deg) rotateX(-8520deg);
        -o-transform: rotateY(-8520deg) rotateX(-8520deg); */
        transform: rotateX(-8820deg);
    }
}

.imgcatActive {
    width: 100%;
    height: 100%;
    /* transition-duration: 0.9s; */
    transition: all 0.1s ease-in;
    transform: rotate(5520deg);
    -webkit-animation: rotating 0.1s linear infinite;
            animation: rotating 0.1s linear infinite;
    transform-style: preserve-3d;
    -webkit-filter: blur(50px);
            filter: blur(50px);
    z-index: 0;
    opacity: 0.8;
    background-size: cover;
}

